.project-list {
  &::before,
  &::after {
    display: block;
    content: '';
    height: 13px;
    background-color: $green;
    //box-shadow: 0 23px 15px -5px rgba(0, 0, 0, 0.2), 0 -23px 15px -5px rgba(0, 0, 0, 0.2);
  }

  &::before {
    box-shadow: inset 0 5px 10px -5px #333;
  }

  &::after {
    box-shadow: inset 0 -5px 10px -5px #333;
  }
}
